import {
    FASTElement,
    html,
    observable,
    customElement,
    css,
    volatile,
} from "@microsoft/fast-element";
import { Message } from "./message.model";

const template = html<FountainSubmit>`
    <form @submit=${x => x.submit()}>

        <label> Amount </label>
        <input type="text" :value=${x => x.message.text} />

        <label> Message </label>
        <textarea
            :value=${x => x.message.text}
            @input=${(x, c) => x.onChangeText(c.event)}
        ></textarea>
        <button
            type="submit"
        >
            SHOUT
            ${x => x.message.text}
            ${x => !x.canSubmit}
        </button>
    </form>
`;

const styles = css`
`;

@customElement({
    name: "fountain-submit",
    template,
    styles,
})
export class FountainSubmit extends FASTElement {

    @observable public message: Message = new Message();

    connectedCallback() {
        super.connectedCallback();
    }

    disconnectedCallback() {
    }

    get canSubmit() {
        return this.message.text != "";
    }

    public submit() {
        if (!this.canSubmit) {
            return;
        }

        this.$emit("submission-action", this.message);
    }

    public onChangeText(event: Event) {
        this.message.text = (event.target! as HTMLInputElement).value;
        this.message = Object.assign({}, this.message);
    }
}
